.tool-tip-copy {
    display: none;
}

.icon {
    width: 100%;
    height: 100%;
    display: block;
}

.tool-tip img {
    width: 100%;
    height: 100%;
    display: block;
}

.tool-tip {
    position: absolute;
    top: 50%;
    left: calc(100% + 2px);
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 15px;
    height: 15px;
    z-index: 2;
    opacity: 0;
    transition: all ease .3s;
}


.tool-tip-copy {
    position: absolute;
    bottom: -200%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background: rgba(241, 241, 241, 0.95);
    color: #000;
    width: 200px;
    height: auto;
    font-size: 1.2rem;
    padding: 2rem;
    border: 1px solid #ccc;
    text-align: center;
    border-radius: 5px;
    -webkit-user-select: none;
            user-select: none;
}

.tool-tip:hover .tool-tip-copy {
    display: block;
}
.items-group {
    margin-bottom: 1rem;
}

.item .tool-tip .tool-tip-copy {
    left: initial;
    right: 0;
    -webkit-transform: initial;
            transform: initial;
}

.item-header {
    font-weight: bold;
    border: 1px solid #ccc;
    padding: 1rem 2rem;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    min-height: 35px;
    line-height: 35px;
    color: #002d56;
}

.item-fields {
    max-width: 100%;
    border-radius: 8px;
    border: 1px solid #ccc;
    font-size: 1.3rem;
    padding: .35rem .8rem;
    display: block;
    width: 100%;
}

.item-labels {
    font-size: 1.1rem;
    width: 100%;
    display: block;
    padding-left: .45rem;
    color: #015289;
}

.input-container {
    padding: .5rem 1.5rem .5rem .5rem;
}

.item:hover .tool-tip {
    opacity: 1;
}

.input-wrapper {
    position: relative;
}

.term-wrapper .row {
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    padding: 1rem;
}

.choices.required{

}
.choices.optional{
    border-left: 2px dotted rgb(209, 209, 209, 1);
    padding-left: 2.5rem;
}

/*Medium Up*/
@media screen and (min-width: 641px){
    .item .tool-tip .tool-tip-copy {
        right: initial;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    
    .item:nth-child(3n) .tool-tip .tool-tip-copy {
        left: initial;
        right: 0;
        -webkit-transform: initial;
                transform: initial;
    }
}
